@charset "UTF-8";

/* Font style setting */
$nanum:"NanumSquare", Arial, Apple SD Gothic Neo, sans-serif;
$rubik:"Rubik", Arial, Apple SD Gothic Neo, sans-serif;

/* Point color setting */
$black:#000;
$white:#fff;

//color
$primary:#2c64cf;
$blue1:#0c1e42;
$blue2:#30405e;
$blue3:#393d45;
$blue4:#596d93;
$blue5:#e9f4ff;
$gray1:#191d25;
$gray2:#696d75;
$gray3:#787b83;
$gray4:#999da5;
$gray5:#c6cace;
$gray6:#e6eaee;
$gray7:#d6dade;
$brown1:#53442d;
$red1 :#de1616;
//condition color
$cdt-blue:#40b0ff;
$cdt-green:#6ed564;
$cdt-orange:#f58c30;
$cdt-red:#d72200;
$cdt-gray1:#e5e5e5;
$cdt-gray2:#999da5;
$cdt-yellow1:#fad889;
$cdt-yellow2:#957f4c;
$cdt-purple:#522891;


/* Background - color setting */
$bg-black:$black;
$bg-gray1:#f5f7f9;
$bg-gray2:#f4f8fb;




/**
 * BASE SETTING
 */

$base-font:'Rubik','NanumSquare', AppleGothic, Arial, Helvetica, sans-serif;
// 본문 사이즈
$base-font-size:16px;
// 본문 color
$base-font-color:$gray1;

$letter-spacing-14-18: -0.5px;
$letter-spacing-19-23: -1px;
$letter-spacing-24-35: -2px;
$letter-spacing-36: -3px;

// font-weight
$light:300;
$regular:400;
$medium:500;
$bold:600;
$ebold:800;

