@charset "UTF-8";
@import "./_common/base";


// 공통 영역 가이드
#guide_html{
   width:100%; min-height:100%; height:100%;
   body{ width:100%; height:100%; }
}

#guide_wrap{
   width:100%; height:100%; max-width: 1920px; margin:0 auto; position:relative;
   // header
   header{
      width: 340px; height:100%; background-color:#071228; padding: 0 45px;color:#fff;
      position:fixed; top:0; left:0; z-index:9999;
      @include boxSizing;
      h1{padding: 40px 0; }
      strong{font-size: 23px; display:block; margin: 0 0 10px;}
      span{ 
         color:#c4c4c4; font-size:17px; letter-spacing: .2px; display:inline-block;
         &.gver{
            font-size:14px; color:#fff; font-weight: $bold; padding:5px 8px; background-color:#e51b24; margin:0 10px;
            @include borderRadius(22px);
         }
      }
      ul{
         padding:30px 0 0; margin:30px 0 0; border-top: 1px solid #394153;
         li{
            margin:12px 0; letter-spacing: .2px;
            a{color:#919191; font-size: 18px; font-weight: $bold;}
            &.active{
               a{color:#fff;}
            }
         }

      }
      
   }
   // common
   .head_title{ font-size:20px; font-weight:$ebold; color:#e51b24; border-bottom:3px solid #e51b24; display:inline-block; padding:0 0 5px;}
   .sub_title{ font-size:18px; font-weight:$bold; color:#16449b; display:block; padding:0 0 18px;}
   .sub_content{margin:60px 0 0;}
   .dashed_line{border-bottom: 2px dashed #d3d3d3; padding: 0 0 60px;}
   .font_r{font-weight:$regular;}
   .font_m{font-weight:$medium;}
   .font_b{font-weight:$bold;}
   .font_eb{font-weight:$ebold;}
   
   //container
   .container{
      width: 1560px; padding:40px 150px; margin:0 0 0 340px;
      .content_wrap{ padding:100px 0 ; border-bottom:1px solid #d3d3d3;}
      @include boxSizing;
      .txt_wrap{
         &>li{
            width:100%; position:relative; margin:0 0 10px;
            p{
               font-size: 30px;
               span{ font-size:16px; font-weight:$regular; color:#167ade; display:inline-block;margin:0 0 0 50px;}
            }
            & > span{
               font-size:17px; color:#e51b24; font-weight: $bold;position:absolute; top:50%; right: 0;
               @include translate(0, -50%);
            }
         }
      }
      &#typeface{
         #fontBalance{
            border-bottom: none;
            .sub_content{
               @extend %clearfix;
               &>div{
                  float:left;
                  li{
                     border-bottom:1px solid #e51b24;
                  }
                  &:nth-of-type(1){
                     width:30%;
                  }
                  &:nth-of-type(2){
                     width:70%;
                  }
               }
            }
         }
         #balancedApplication{
            .txt_wrap{
               color:#191d25; padding:0 50px 0;
               @include boxSizing;
               &>strong{font-weight:$medium; font-size:24px; line-height:36px;}
               &>p{font-weight:$regular; font-size:15px; line-height:27px; letter-spacing:-.5px}
               ul{
                  margin:40px 0 0;
                  @extend %clearfix;
                  li{
                     width:340px; height:140px ;float:left; background-color:#f5f7f9; margin: 0 20px 0 0; padding:40px 30px; text-align:center; color:#0c1e42;
                     @include boxSizing;
                     &:nth-of-type(1){
                        text-align:left;
                        p{font-size: 22px; font-weight:$bold; letter-spacing:-1px;}
                        strong{font-size: 30px; font-weight:$ebold; line-height:42px; letter-spacing:-2px}
                     }
                     &:nth-of-type(2){
                        span{font-size: 17px; font-weight:$regular; letter-spacing:-1px; display:block; color:#696d75; line-height:29px;}
                        strong{font-size: 32px; font-weight:$bold; line-height:44px; letter-spacing:-2px}
                     }
                     &:nth-of-type(3){
                        span{font-size: 17px; font-weight:$regular; letter-spacing:-1px; display:inline-block; color:#696d75; line-height:29px; margin:0 10px 0;}

                     }
                  }
               }
            }
         }
         #standardFontSize{
            .sub_content{
               @extend %clearfix;
               &>div{
                  width:50%; float:left; padding: 0 0 0 50px;
                  @include boxSizing;
                  &:nth-of-type(1){
                     p{font-size:15px; color:#393d45; letter-spacing:-.5px; line-height:27px;}
                     span{ display:inline-block; font-size: 14px; color:#a6aaae; margin:0 0 0 20px; }
                     &.haed_font01{
                        strong{font-size:36px; color:#16449b; line-height:48px; font-weight:$ebold;}
                        p{font-size:36px; color:#0c1e42; font-weight:$bold; line-height:48px;}
                     }
                     &.haed_font02{
                        strong{font-size:32px; color:#16449b; line-height:44px; font-weight:$ebold;}
                        p{font-size:36px; color:#0c1e42; font-weight:$bold; line-height:48px;}
                     }
                     &.haed_font03{
                        strong{font-size:22px; color:#16449b; line-height:36px; font-weight:$ebold;}
                        p{font-size:36px; color:#0c1e42; font-weight:$bold; line-height:48px;}
                     }
                  }
                  &:nth-of-type(2){
                     ul{
                        display: inline-block; margin:0 0 0 50px;
                        li{
                           font-size: 13px;
                           span{
                              width: 20px; height: 20px; background-color:#f00; font-weight: $bold; text-align:center; line-height:20px; display:inline-block; color:#fff; margin:0 5px 10px 0;
                              @include borderRadius(20px);
                           }
                           strong{color:#ff4242;  font-weight: $ebold;}
                           div{ display: inline; color:#0c1e42;  font-weight: $ebold;}
                        }
                     }
                  }
               }
            }
         }

         #rulesofUse{
            border-bottom: none;
            .sub_content{
               &>div{
                  &>strong{font-size:36px; color:#80169b; line-height:48px; font-weight:$ebold;}
                  &>span{display:inline-block; font-size: 14px; color:#a6aaae; margin:0 0 0 20px;}
                  &:nth-of-type(1){
                     margin:0 0 80px;
                     ul{
                        li{
                           font-size:30px; color:#0c1e42; line-height:42px; font-weight:$bold; margin:10px 0;
                           div{display:inline-block; font-weight:$ebold;}
                           span{display:inline-block; font-size: 14px; color:#e51b24; font-weight:$bold; margin:0 0 0 20px;}
                        }
                     }
                     &.rul_dont{
                        strong{color:#b12121;}
                        select{
                           width:120px; display:inline-block; font-size:14px; color:#696d75; font-weight: $bold; padding:8px 0 8px 8px; border: 1px solid #b6babe;
                        }
                        .select_wrap{
                           display: inline-block; position:relative; margin:0 0 0 15px;
                           &:after{
                              width:10px; height:10px; content:""; display:block; border-top: 2px solid #b6babe; border-right: 2px solid #b6babe;
                              position:absolute; top: 18px; right: 15px;
                              @include rotate(135);
                           }
                        }
                     }
                  }

                  &:nth-of-type(2){
                     ul{
                        margin:20px 0 0;
                        @extend %clearfix;
                        li{
                           width:20%; float:left;
                           @extend %clearfix;
                           div{
                              float:left; margin:0 0 15px;
                              &:nth-of-type(1){
                                 width:40%;
                                 strong{font-size:15px; line-height:27px; color:#167ade;  display:block; font-weight: $ebold;}
                                 span{font-size:24px; line-height:36px; color:#0c1e42;  display:block; opacity:.4; font-weight: $ebold;}
                              }
                              &:nth-of-type(2){
                                 width:60%;
                                 strong{font-size:15px; line-height:27px; color:#f00;  display:block; font-weight: $ebold;}
                                 span{font-size:24px; line-height:36px; color:#0c1e42;  display:block; font-weight: $ebold;}
                              }
                           }

                        }
                     }
                  }
               }


            }
         }
      }

      &#color{
         .bg_border_wrap{display: inline-block; vertical-align:top; margin:0 0 0 40px;}
         .color_box_wrap{
            display: inline-block; margin:0 0 0 15px; vertical-align:top;
            .color_box{
               width: 80px; height: 80px; margin:0 0 10px; font-weight: $bold;
               padding:15px; color:#fff; position:relative;
               @include borderRadius(5px);
               @include boxSizing;
               &>div{
                  width:80px; height:80px; position:absolute; top:5px; left:-5px;
                  @include borderRadius(5px);
               }
               span{
                  position:absolute; bottom:15px; right:15px;
               }
            }
            .color_hex{font-size:15px; font-weight: $ebold; line-height: 27px; color:#1c1d1e;}
            .color_rgb{font-size:13px; color:#999da5;}
         }

         #primary{
            @extend %clearfix;
            &>div{
               width: 50%; float:left;
               .txt_wrap{
                  display:inline-block; vertical-align:top;
                  strong{font-size:18px; color:#0c1e42; line-height:30px; letter-spacing: -.5px;}
                  p{ font-size:14px; color:#4a7297; line-height:22px; font-weight: $bold; margin:10px 0 0;}
               }
               &:nth-of-type(1){
                  .txt_wrap{margin:0 0 0 20px;}
               }
               &:nth-of-type(2){
                  .txt_wrap{margin:0 20px 0 0;}
               }
            }
         }

         #strokeOnly{
            @extend %clearfix;
            &>div{
                float:left;
               &:nth-of-type(1){
                  width: 30%;
               }
               &:nth-of-type(2){
                  width: 70%;
               }
            }
         }
      }

   }
}

